body {
  margin: 0;
  padding: 0px;
}

#root {
  margin: 0 auto;
  text-align: center;
  overflow-x: hidden;
}

:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #313030;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

.App {
  padding: 0 5px;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(44, 44, 44);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #b6b6b6;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #bdbdbd;
}

.LinkStyles {
  color: white;
  text-decoration: none;
  margin: 0 10px;
}


.ContentBox {
  display: grid;
  height: 100%;
  width: 96%;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: repeat(4, 25%);
}

.MyPhoto {
  filter: grayscale(100%);
  position: fixed;
  bottom: 0%;
  left: 150px;
  width: 35%;
  background-color: #313030;
}

.MyPhotoAnimation {
  animation: PhotoApear 2s;
}

@keyframes PhotoApear {
  from {
    opacity: 0%;
    position: fixed;
    bottom: 0%;
    left: -250px;
  }

  to {
    position: fixed;
    opacity: 100%;
    bottom: 0%;
    left: 150px;
  }

}


.TextBoxAbout {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 6;
  text-align: left;
  max-width: 700px;
  z-index: 20;
  background-color: #313030;
  padding: 10px 0;
}


.Fixed {
  position: fixed;
  top: calc(50% - 236px);
  left: 50%;
  text-align: left;
  max-width: 700px;
  padding: 15px;
  height: 700px;
  z-index: 10;
  background-color: #313030;
}

.AboutContent {
  height: 100vh;
  width: 100vw;
}

.TextBoxHome {
  position: fixed;
  width: 40%;
  max-width: 700px;
  top: calc(50% - 129px);
  left: 50%;
  margin-left: 5px;
  text-align: left;
  /* grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 4; */
  padding: 15px;
  border: 1px double white;
}

.boxOuter {
  padding: 5px;
  border: 2px solid #00C2FF;
}

.boxOuter:before,
.boxOuter:after {
  content: "•";
  position: absolute;
  width: 14px;
  height: 14px;
  font-size: 14px;
  color: #00C2FF;
  border: 2px solid #00C2FF;
  line-height: 12px;
  top: 5px;
  text-align: center;
  z-index: 5;
}


.boxOuter:before {
  left: 5px;
}

.boxOuter:after {
  right: 5px;
}

.boxOuter .boxInner {
  position: relative;
  border: 2px solid #00C2FF;
  padding: 40px;
}

.boxInner {
  background-color: #2c2c2c;

}

.boxInner:before,
.boxInner:after {
  content: "•";
  position: absolute;
  width: 14px;
  height: 14px;
  font-size: 14px;
  color: #00C2FF;
  border: 2px solid #00C2FF;
  line-height: 12px;
  bottom: -2px;
  text-align: center;
}

.boxInner:before {
  left: -2px;
}

.boxInner:after {
  right: -2px;
}


.AboutLinks {
  color: #00C2FF;
  text-decoration: 1.5px underline;
}

.AboutLinks:hover,
.AboutLinks:active {
  color: white;
}

.ProjectsPage {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: nowrap;
  align-items: center;
  /* margin: 24px 0; */
  height: 100vh;
}


.Sticky {
  position: sticky;
  top: 0;

}

.BackgroundDiff {
  background-color: #313030;

}

.BackgroundDiff:nth-child(2),
.BackgroundDiff:nth-child(4) {
  background-color: #4B4B4B;
}

.ProjectThumbnailPhoto {
  max-width: 400px;
  width: 400px;
  height: auto;
  margin: .5px;
  filter: grayscale(100%);
  filter: opacity(60%);
  border-radius: 25px;
}


.LearnMore {
  text-decoration: none;
}

.LearnMoreBtn {
  color: rgb(255, 255, 255);
  background-color: white;
  font-weight: 700;
  text-decoration: underline;
  font-size: 2rem;
  position: relative;
  top: calc(50% - 12px);
  margin: 0 auto;
  z-index: 100;
  height: 0;
}

.DisplayNone {
  display: none;
}


.footerSocMedia {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px 0;
}

.footerSocMedia li {
  margin: 0 5px;
}

.footerSocMedia li a {
  color: white;
}

svg.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.css-okbo41-MuiSvgIcon-root:hover,
svg.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.css-okbo41-MuiSvgIcon-root:active {
  color: #00C2FF;
  animation: socMediaHover 500ms;
}

@keyframes socMediaHover {
  from {
    transform: scale(1, 1);
  }

  30% {
    transform: scale(.90, .90);
  }

  60% {
    transform: scale(1.2, 1.2);
  }

  to {
    transform: scale(1, 1);
  }
}

.learnMoreHover {
  background-color: red;
}


.BtnTo650px {
  display: none;
}

.ProjectPhotosBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.LinkNextPreviousProject {
  color: white;
  text-decoration: none;
}

.LinkNextPreviousProject:hover,
.LinkNextPreviousProject:active {
  text-decoration: underline;
}

.LinkGithubProject {
  color: white;
  text-decoration: none;
}

svg.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.css-10lsgvu-MuiSvgIcon-root:hover,
svg.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.css-10lsgvu-MuiSvgIcon-root:active {
  color: #00C2FF;
  animation: GithubLinkAnimation 500ms;
}

@keyframes GithubLinkAnimation {
  from {
    transform: scale(1, 1);
  }

  30% {
    transform: scale(.90, .90);
  }

  60% {
    transform: scale(1.2, 1.2);
  }

  to {
    transform: scale(1, 1);
  }
}

@media (max-width: 650px) {
  .boxOuter {
    border: none;
    width: fit-content;
  }

  .ContentBox {
    display: block;
  }

  .TextBoxAbout,
  .Fixed {
    display: inline-block;
  }

  .AboutContent {
    height: 100%;
  }

  .ProjectPhotosBox {
    flex-direction: column;
  }

  .MyPhoto {
    display: block;
    position: inherit;
    margin: 0 auto;
    max-width: 60%;
    width: -moz-fit-content;
    width: fit-content;
    min-width: 60%;
  }

  .MyPhotoAnimation {
    animation: PhotoApear 2s;
  }

  @keyframes PhotoApear {
    from {
      opacity: 0%;
      position: inherit;
    }

    to {

      position: inherit;
      opacity: 100%;
    }

  }


  .BtnFrom650px {
    display: none;
  }

  .BtnTo650px {
    display: block;
  }

  .ProjectImg {
    max-width: 96%;
  }

  .ProjectImg img {
    width: 300px;
  }
}

@media (max-width: 1200px) {
  body {
    max-width: 96%;
    padding: 0 2%
  }

  .PhotoBox {
    height: 100%;
    display: inline-block;
  }

  .MyPhoto {
    display: block;
    position: inherit;
    margin: 0 auto;
    max-width: 60%;
    width: fit-content;
    min-width: 60%;
  }

  .MyPhotoAnimation {
    animation: PhotoApear 2s;
  }

  @keyframes PhotoApear {
    from {
      opacity: 0%;
      position: inherit;
    }

    to {

      position: inherit;
      opacity: 100%;
    }

  }

  .MyPhotoAbout {
    display: none;
  }

  .ContentBox {
    display: block;
    width: 96%;
    margin: 15px 0 0 0;
  }

  .TextBoxHome,
  .TextBoxAbout,
  .Fixed {
    width: 90%;
    position: unset;
    margin: auto;
    padding: 15px;
    margin-top: 20px;
    margin: 15px auto 0;
  }

  .Fixed {
    margin-top: 0;
    padding-top: 0;
  }

  .ProjectThumbnailPhoto {
    width: fit-content;
    max-width: 350px;
    height: auto;
    border-radius: 25px;
  }

  .ProjectThumb {
    width: fit-content;
    filter: grayscale(0%);
    margin: 0 auto;
  }

  .ProjectThumb:hover,
  .ProjectThumb:active {
    filter: grayscale(100%);
  }

  .todo,
  .calculator,
  .spending,
  .stackOver,
  .eshop {
    position: inherit;
  }

  .boxOuter:before,
  .boxOuter:after,
  .boxInner:before,
  .boxInner:after {
    display: none;
  }
}

@media (min-width: 650px) and (max-width: 1200px) {

  .ProjectImg {
    max-width: 96%;
  }

  .ProjectImg img {
    width: 600px;
  }

}

.DisplayNone {
  display: none;
}

.LoadingScreen {
  background-color: #2c2c2c;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100000;
}

.LoadingBox {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.Loading {
  width: 30px;
  height: 30px;
  background-color: white;
  margin: 10px 5px;
  border-radius: 100%;
}

.Ball1 {
  animation: Hopping 1s infinite 0ms ease-in-out;
}

.Ball2 {
  animation: Hopping 1s infinite 250ms ease-in-out;
}

.Ball3 {
  animation: Hopping 1s infinite 400ms ease-in-out;
}

@keyframes Hopping {
  0% {
    transform: translateY(0%);
  }

  55% {
    transform: translateY(-100%);
  }

  90% {
    transform: translateY(0%);
  }
}